body {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.wrap-main {
  display: flex;
  height: 100%; }

.side-panel {
  width: 23.7%; }
  .side-panel .top-logo {
    height: 71px;
    background: #D1D4D9; }
    .side-panel .top-logo .logo-img {
      background: url(../img/logo_black.svg) no-repeat;
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: 80%;
      display: inline-block; }
  .side-panel .navigation-city {
    background: #033953;
    padding: 26px 10px 45px 131px; }
    .side-panel .navigation-city .city {
      display: block;
      font-family: 'Gotham Pro';
      line-height: 46px;
      font-size: 1.7vw;
      font-weight: 300;
      color: #FFFFFF;
      text-decoration: none;
      position: relative;
      transition: color .5s; }
      .side-panel .navigation-city .city:hover {
        color: #DC143C; }
      .side-panel .navigation-city .city:first-child {
        margin-top: 23px; }
      .side-panel .navigation-city .city:first-child:before {
        content: "";
        position: absolute;
        width: 56.83px;
        height: 56.83px;
        background: url(../img/moscow.svg) no-repeat;
        left: -78px;
        top: -6px;
        background-size: 100%; }
      .side-panel .navigation-city .city:nth-child(2) {
        margin-top: 43px; }
      .side-panel .navigation-city .city:nth-child(2):before {
        content: "";
        position: absolute;
        width: 56.83px;
        height: 56.83px;
        background: url(../img/spb.svg) no-repeat;
        left: -77px;
        top: -6px;
        background-size: 100%; }
      .side-panel .navigation-city .city:last-child {
        margin-top: 42px; }
      .side-panel .navigation-city .city:last-child:before {
        content: "";
        position: absolute;
        width: 56.83px;
        height: 56.83px;
        background: url(../img/saratov.svg) no-repeat;
        left: -78px;
        top: -3px;
        background-size: 100%; }

.content-sidebar {
  padding: 32px 55px 33px 55px;
  background: #314A5D;
  font-family: 'Gotham Pro'; }
  .content-sidebar .content-sidebar-title {
    line-height: 52px;
    font-size: 2.5vw;
    color: #FFFFFF;
    font-weight: normal;
    margin: 0; }
  .content-sidebar .content-sidebar-description {
    line-height: 26px;
    font-size: .95vw;
    color: #FFFFFF;
    font-weight: lighter;
    padding-top: 26px;
    margin: 0;
    padding-bottom: 20px; }
  .content-sidebar .read-more {
    padding-top: 10px;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.6);
    font-size: 16px;
    padding: 10px 0;
    transition: color 0.3s; }
    .content-sidebar .read-more:hover {
      color: #DC143C;
      opacity: 1; }

.wepb .content-part {
  /*background: url(../img/1_bg_min.webp) no-repeat;*/ }

.content-part {
  width: 76.3%;
