    right: 58px;
    width: 40%;
    height: 30%; }

.main-slider-bg {
  background: rgba(87, 114, 131, 0.5);
  background-blend-mode: multiply;
  position: absolute;
  width: 1015px;
  height: 201px;
  bottom: 0;
  right: 0; }
  .main-slider-bg .slick-slider {
    position: absolute;
    width: 1020px;
    height: 195px;
    bottom: 0;
    right: -17px; }
    .main-slider-bg .slick-slider .image-block {
      outline: none; }
    .main-slider-bg .slick-slider .image-block-item {
      width: 190px;
      height: 194px;
      border: 1px solid #7F96A8;
      border-bottom: none; }
    .main-slider-bg .slick-slider .slick-prev {
      left: 10px;
      z-index: 99; }
    .main-slider-bg .slick-slider .slick-next {
      display: none !important; }

.read-more-popup {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100; }

.read-more-show {
  display: flex; }

.read-more-sidebar {
  width: 23.7%;
  background: #033953;
  opacity: .5; }

.read-more-content {
  position: relative;
  width: 76.3%;
  background: #033953;
  color: #fff; }
  .read-more-content .close-read-more {
    content: "";
    position: absolute;
    right: 40px;
    top: 31px;
    transition: transform .3s; }
    .read-more-content .close-read-more:hover {
      transform: scale(1.3); }
